.xxx-show-start {
  opacity: 0;
}
.xxx-show-end {
  opacity: 1;
  transition: opacity 200ms;
}
.xxx-exit {
  opacity: 1;
}
.xxx-hide-end {
  opacity: 0;
  transition: opacity 350ms;
}

.xxx-delay-show-start {
  opacity: 0;
}
.xxx-delay-show-end {
  opacity: 1;
  transition: opacity 750ms 250ms;
}
.xxx-delay-exit {
  opacity: 1;
}
.xxx-delay-hide-end {
  opacity: 0;
  transition: opacity 750ms 250ms;
}

.xxx-timings-show-start {
  opacity: 0;
}
.xxx-timings-show-end {
  opacity: 1;
  transition: opacity 200ms;
}
.xxx-timings-exit {
  opacity: 1;
}
.xxx-timings-hide-end {
  opacity: 0;
  transition: opacity 800ms;
}
